<template>
  <div class="moment-view">
    <pt-button @click="cancel">cancel</pt-button>
    <div>
      <pt-radio-button
        v-for="eventType in eventTypes"
        :key="eventType"
        v-model="selectedEventType"
        :value="eventType"
      >
        {{ eventType }}
      </pt-radio-button>
    </div>
    <pt-button @click="createMoment">Create Moment</pt-button>
  </div>
</template>

<script>
import { EventTypes } from '../enums/moment'

export default {
  name: 'MomentView',
  components: {},
  mixins: [],
  props: {},
  data: () => ({
    selectedEventType: null,
    eventTypes: Object.keys(EventTypes)
  }),
  created () {},
  mounted () {},
  updated () {},
  destroyed () {},
  watch: {},
  computed: {},
  methods: {
    createMoment () {
      this.$appService.send('CREATE', { eventType: EventTypes[this.selectedEventType] })
    },
    cancel () {
      this.$appService.send('CANCEL')
    }
  },
  directives: {},
  filters: {},
}
</script>

