<template>
  <div class="kennel-view">
    <pt-button @click="goBack">back</pt-button>
    <h1>{{ name }}</h1>
    <div>
      <h2>Timeline</h2>
      <div class="timeline">
        <template
          v-for="event in events"
        >
          <div :key="'time' + event.date">
            {{ event.type }}
          </div>
          <div :key="'type' + event.date">
            {{ event.date | date }}
          </div>
        </template>
      </div>
    </div>
    <pt-button @click="goToMomentCreation">Create Moment</pt-button>
  </div>
</template>

<script>
export default {
  name: 'KennelView',
  components: {},
  mixins: [],
  props: {},
  data: () => ({}),
  created () {},
  mounted () {},
  updated () {},
  destroyed () {},
  watch: {},
  computed: {
    context () {
      return this.$appService.state.context
    },
    homeAt () {
      const homeAt = this.context.homeAt && this.context.homeAt.id
      return homeAt && this.$appService.children.get(homeAt)
    },
    petWith () {
      const petWith = this.context.petWith.id
      if (this.homeAt) return this.homeAt.children.get(petWith)
      return this.$appService.children.get(petWith)
    },
    petWithContext () {
      return this.petWith.state.context
    },
    pet () {
      return this.petWithContext.pet
    },
    name () {
      return this.pet.name
    },
    timeline () {
      return this.pet.timeline
    },
    events () {
      return this.timeline.events
    },
    activities () {
      return this.timeline.activities
    }
  },
  methods: {
    goToMomentCreation () {
      this.$appService.send('CREATE_MOMENT')
    },
    goBack () {
      this.$appService.send('BACK_HOME')
    }
  },
  directives: {},
  filters: {},
}
</script>

<style lang="scss">
.timeline {
  display: grid;
  grid-template-columns: max-content max-content;
  grid-gap: 1rem;
}
</style>
